.snext-search-wrapper {
    position: relative;
    margin-bottom: 16px;
}

.snext-search {
    width: 90%;
    padding: 10px;
    border-radius: 8px;
    border: none;
    background: #2e2e57;
    color: #fff;
    font-size: 1rem;
    margin-bottom: 24px;
    outline: none;
}

.snext-search-clear {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: #b3b8c5;
    font-size: 1.3em;
    cursor: pointer;
    padding: 0;
    z-index: 2;
}

.snext-search-chip-row {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 8px;
    margin-left: 2px;
}

.snext-search-chip {
    background: #2e2e57;
    color: #fff;
    border-radius: 18px;
    padding: 4px 14px 4px 10px;
    font-weight: 600;
    font-size: 1em;
    display: flex;
    align-items: center;
    gap: 6px;
}

.snext-search-chip-x {
    background: none;
    border: none;
    color: #b3b8c5;
    font-size: 1.1em;
    margin-left: 4px;
    cursor: pointer;
    padding: 0 2px;
}

.snext-search-clearall {
    display: block;
    width: 90%;
    margin: 18px auto 0 auto;
    padding: 10px 0;
    background: #29295a;
    color: #fff;
    font-weight: 700;
    font-size: 1.08em;
    border: none;
    border-radius: 10px;
    text-align: center;
    cursor: pointer;
    transition: background 0.18s, color 0.18s, box-shadow 0.18s;
    box-shadow: 0 2px 8px #0002;
    letter-spacing: 0.5px;
}

.snext-search-clearall:hover {
    background: #35357a;
    box-shadow: 0 4px 16px #0003;
}

.snext-search-clearall-x {
    margin-left: 10px;
    text-decoration: none !important;
}