.footer {
    background: linear-gradient(135deg, #1d1d37 0%, #23234a 100%);
    color: #eaeaff;
    font-family: system-ui, sans-serif;
}

.footer-container {
    display: grid;
    grid-template-columns: 0.8fr 1.7fr;
    height: 500px;
    overflow: hidden;
    gap: 3rem;
}

.footer-left {
    display: grid;
    grid-template-rows: auto 1fr;
    justify-items: center;
    align-items: start;
    height: 100%;
    padding: 0;
    margin-top: 70px;
    margin-left: 40px;
}

.footer-left-top {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-left-bottom {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0;
}

.footer-brand {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    padding: 0;
}

.footer-logo img {
    height: 100px;
}

.footer-title {
    font-size: 1.2rem;
    font-weight: 600;
}

.footer-links {
    display: flex;
}

.footer-column h4 {
    font-size: 1.5rem;
    margin-bottom: 0.75rem;
    color: #ffffff;
}

.footer-column a {
    display: block;
    color: #b7b7d9;
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    text-decoration: none;
}

.footer-column a:hover {
    color: #14a0e1;
}


.footer-social-icons {
    margin-top: 1rem;
    display: flex;
    gap: 1.5rem;
    justify-content: center;
}

.footer-social-icons a {
    color: #b7b7d9;
    transition: color 0.2s;
    margin-left: 12px;
    vertical-align: middle;
}

.footer-social-icons a:hover {
    color: #14a0e1;
}

.footer-right {
    background: linear-gradient(180deg,
            #0e1735 0%,
            #102758 40%,
            #16356b 75%,
            #3260b6 100%);
    display: grid;
    grid-template-columns: 1.2fr 1fr;
    grid-template-rows: 1fr auto;
    border-left: 4px solid #181c24;
}

.footer-promo {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    padding-left: 50px;
}

.footer-promo h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.footer-promo p {
    font-size: 0.95rem;
    color: #cfcff5;
    margin-bottom: 1.5rem;
    line-height: 1.5;
}

.promo-button {
    display: inline-block;
    background: #14a0e1 !important;
    color: #0b0b1f !important;
    text-decoration: none !important;
    outline: none !important;
    padding: 0.7rem 1.4rem;
    border-radius: 8px;
    font-weight: 600;
    transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.promo-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(74, 228, 255, 0.3);
    color: #0b0b1f !important;
    text-decoration: none !important;
    outline: none !important;
}


.footer-image {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    align-self: end;
}

.footer-image img {
    width: 140%;
    max-width: none;
    transform: translateX(-30%);
    box-shadow: 0 32px 96px 0 rgba(0, 0, 0, 0.85), 0 8px 32px 0 rgba(0, 0, 0, 0.45);
}

.footer-bottom {
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-top: 4px solid #181c24;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    color: #a5a5c9;
}

.footer-bottom-links {
    display: flex;
    gap: 1.5rem;
}

.footer-bottom-links a {
    color: inherit;
    text-decoration: none;
}

.footer-bottom-links a:hover {
    color: #ffffff;
}

@media (max-width: 900px) {
    .footer-container {
        grid-template-columns: 1fr;
    }

    .footer-right {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .footer-image {
        display: none;
    }

    .footer-bottom {
        flex-direction: column;
        gap: 1rem;
        text-align: center;
    }
}